import $ from 'jquery'

const matDisplayStyles = {
  augmented: 'augmented',
  parentheses: 'parentheses',
  determinant: 'determinant',
  augmentedWithParentheses: 'augmentedWithParentheses',
}

let resultsDiv
$(window).on('load', () => {
  resultsDiv = $('.results-div')
})

//!Helper Functions (arrOfArrToMat)
const arrOfArraysToMat = (mat, matStyleClass) => {
  let table = document.createElement('table')
  let tableBody = document.createElement('tbody')
  let cell, cellText

  // create the table body:
  mat.forEach((resultsRow) => {
    let row = document.createElement('tr')
    resultsRow.forEach((element) => {
      cell = document.createElement('td')
      cellText = document.createTextNode(element)
      cell.classList.add('browser-default')
      cell.appendChild(cellText)

      row.appendChild(cell)
    })
    row.classList.add('browser-default')
    tableBody.appendChild(row)
  })
  tableBody.classList.add('browser-default')
  table.appendChild(tableBody)
  //
  table.classList.add('matrix', 'browser-default', matStyleClass)
  return table
}

const arrToMatVertical = (mat, matStyleClass) => {
  let table = document.createElement('table')
  let tableBody = document.createElement('tbody')
  let cell, cellText

  // create the table body:
  mat.forEach((matRow) => {
    let row = document.createElement('tr')

    cell = document.createElement('td')
    cellText = document.createTextNode(matRow)
    cell.classList.add('browser-default')
    cell.appendChild(cellText)

    row.appendChild(cell)

    row.classList.add('browser-default')
    tableBody.appendChild(row)
  })
  tableBody.classList.add('browser-default')
  table.appendChild(tableBody)
  //
  table.classList.add('matrix', 'browser-default', matStyleClass)
  return table
}
const arrToMatHorizontal = (mat, matStyleClass) => {
  let table = document.createElement('table')
  let tableBody = document.createElement('tbody')
  let row = document.createElement('tr')
  let cell, cellText

  // create the table body:
  mat.forEach((element) => {
    cell = document.createElement('td')
    cellText = document.createTextNode(element)
    cell.classList.add('browser-default')
    cell.appendChild(cellText)
    row.appendChild(cell)
  })
  row.classList.add('browser-default')
  tableBody.appendChild(row)
  tableBody.classList.add('browser-default')
  table.appendChild(tableBody)
  //
  table.classList.add('matrix', 'browser-default', matStyleClass)
  return table
}

function mathJaxRender() {
  MathJax.typesetClear()
  MathJax.typeset()
}

//==> to display (e.g.) -> m21 = 1/2 = 0.5
function mXY(x, y, fractionStr, decimalNum) {
  return `
            <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">
                \`m_{${x + '' + y}} = ${fractionStr} = ${decimalNum}\`
            </p>
        `
}
function paragraph(string) {
  return `
            <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">
               \`${string}\`
            </p>
        `
}
//=================================================================================//

export const gaussHtml = ({ results }) => {
  //==> print the input matrix :
  resultsDiv.append(paragraph('[ A/B ] = '))

  resultsDiv.append(arrOfArraysToMat(results.matrix, 'augmented'))
  resultsDiv.append(`<br><br>`)

  //!=====================(step 1)=====================!//
  resultsDiv.append(paragraph('STEP 1 : Calc m21 and m31 '))

  //==> Append m21:
  resultsDiv.append(mXY(2, 1, results.m21String, results.m21))

  //==> Append m31:
  resultsDiv.append(mXY(3, 1, results.m31String, results.m31))

  //==> display what to do to get rid of  the first zero:
  resultsDiv.append(paragraph('STEP 2 : Calc  Row 2 '))

  resultsDiv.append(
    `
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">
        \`R_{2} - (m_{21})R_{1} -> R_{2}\`
      </p>
    `
  )

  //==> display what to do to get rid of  the second zero:
  resultsDiv.append(paragraph('STEP 3 : Calc  Row 3 '))

  resultsDiv.append(
    `
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">
        \`R_{3} - (m_{31})R_{1} -> R_{3}\`
      </p>
    `
  )
  //==> print augmented matrix after the 1 zero:
  resultsDiv.append(paragraph(' [ A/B ] = >  '))

  resultsDiv.append(arrOfArraysToMat(results.matrixWithR2R3, 'augmented'))
  resultsDiv.append(`<br><br>`)

  //!=====================(step 2)=====================!//
  //==> Append m32:
  resultsDiv.append(paragraph('STEP 4 : Calc  m21 and m32 '))

  resultsDiv.append(mXY(3, 2, results.m32String, results.m32))

  //==> display what to do to get rid of  the third zero:
  resultsDiv.append(paragraph('STEP 5 : Calc  Row 3 '))

  resultsDiv.append(
    `
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">
        \`R_{3} - (m_{32})R_{1} -> R_{3}\`
      </p>
    `
  )

  //==> print augmented matrix after the 3 zeroes:
  resultsDiv.append(paragraph(' [ A/B ] = >  '))

  resultsDiv.append(arrOfArraysToMat(results.newMatrix[0], 'augmented'))
  resultsDiv.append(`<br><br>`)

  //display x1,x2,x3:
  resultsDiv.append(('solve using back substitution   '))

  resultsDiv.append(
    `
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">\`x_{1}=${results.x1}\`</p>
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">\`x_{2}=${results.x2}\`</p>
      <p style="margin-top:2px; margin-bottom: 2px; font-size:120%; font-weight:bold;">\`x_{3}=${results.x3}\`</p>
    `
  )
  mathJaxRender()
}

export const luHtml = ({ results }) => {
  //==>Print A:
  resultsDiv.append(paragraph('A='))
  resultsDiv.append(`<br>`)

  resultsDiv.append(('A = '), arrOfArraysToMat(results.a, 'augmented'))
  resultsDiv.append(`<br><br>`)

  //==>Print B:
  resultsDiv.append(paragraph('B='))
  resultsDiv.append(`<br>`)

  resultsDiv.append(('B = '), arrToMatVertical(results.b, 'augmented'))
  resultsDiv.append(`<br><br>`)
  //==>Lower MAtrix 
  resultsDiv.append(paragraph('L='))
  resultsDiv.append(`<br>`)

  let L = [[1, 0, 0], ['m21', 1, 0], ['m31', 'm32', 1]]
  resultsDiv.append(('L = '), arrOfArraysToMat(L, 'augmented'), (' = '), arrOfArraysToMat(results.lowerMatrix, 'augmented'))

  resultsDiv.append(`<br><br>`)
  //==> U
  resultsDiv.append(paragraph('U = '))
  resultsDiv.append(`<br>`)

  resultsDiv.append(arrOfArraysToMat(results.upperMatrix, 'augmented'))
  resultsDiv.append(`<br><br>`)
  //==? LC=B
  resultsDiv.append(paragraph('LC = B'))
  resultsDiv.append(`<br>`)

  resultsDiv.append(arrOfArraysToMat(results.lowerMatrix, 'augmented'), '  ', arrToMatVertical(['c1', 'c2', 'c3'], 'augmented'), ' = ', arrToMatVertical(results.b, 'augmented'))
  //
  resultsDiv.append(paragraph('  C = '))
  resultsDiv.append(`<br>`)

  resultsDiv.append(arrToMatVertical(['c1', 'c2', 'c3'], 'augmented'), (' = '), arrToMatVertical([results.c1, results.c2, results.c3], 'augmented'))
  // UX=C
  resultsDiv.append(paragraph(' UX = C'))
  resultsDiv.append(`<br>`)
  resultsDiv.append(arrOfArraysToMat(results.upperMatrix, 'augmented'), (' * '), arrToMatVertical(['x1', 'x2', 'x3'], 'augmented'), (' = '), arrToMatVertical([results.c1, results.c2, results.c3], 'augmented'))
  resultsDiv.append(`<br>`)
  resultsDiv.append(`<br>`)
  // X 
  resultsDiv.append(arrToMatVertical(['x1', 'x2', 'x3'], 'augmented'), (' = '), arrToMatVertical([results.x1, results.x2, results.x3], 'augmented'))

  mathJaxRender()
}

export const crammerHtml = ({ results }) => {

  resultsDiv.append(paragraph('A = '))
  resultsDiv.append(`<br>`)

  resultsDiv.append(('A = '), arrOfArraysToMat(results.detResult[0].a, 'determinant'))
  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('B = '))
  resultsDiv.append(`<br>`)

  resultsDiv.append(arrToMatVertical(results.detResult[0].b, 'augmented'))
  resultsDiv.append(`<br><br>`)

  resultsDiv.append(paragraph('|D| = '))
  resultsDiv.append(arrOfArraysToMat(results.detResult[0].a, 'determinant'))
  resultsDiv.append(paragraph('= ' + results.detResult[0].detA))
  resultsDiv.append(`<br><br>`)

  resultsDiv.append(paragraph('|D1| = '))
  resultsDiv.append(arrOfArraysToMat(results.detResult[1].detMatrix, 'determinant'))
  resultsDiv.append(paragraph('= ' + results.detResult[1].det))

  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('|D2| = '))
  resultsDiv.append(arrOfArraysToMat(results.detResult[2].detMatrix, 'determinant'))
  resultsDiv.append(paragraph('= ' + results.detResult[2].det))

  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('|D3| = '))
  resultsDiv.append(arrOfArraysToMat(results.detResult[3].detMatrix, 'determinant'))
  resultsDiv.append(paragraph('= ' + results.detResult[3].det))
  //x1
  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('x1 = (D1)/D  = ' + results.x1))
  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('x2 = (D2)/D  = ' + results.x2))
  resultsDiv.append(`<br><br>`)
  resultsDiv.append(paragraph('x3 = (D3)/D  = ' + results.x3))




  mathJaxRender()
}

export const hessianHtml = ({ results }) => {
  // create the table body:

  let table = document.createElement('table')
  let tableBody = document.createElement('tbody')
  let cell, cellText, row
  //1
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode('Gradient and hessian')
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //2
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` nabla f = ( partial f) / (partial x) i+( partial f) / (partial y) j \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //3
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`( partial f) / (partial x) = ${results.dfdx1} = ${results.edfdx1} \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //4
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`( partial f) / (partial y) = ${results.dfdy1} =  ${results.edfdy1} \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //5
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` nabla f = ${results.gradient} \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` |H| = \``)
  let matrix = [[`\`  ( partial^2 f) / (partial x^2) \``, `\`  ( partial^2 f) / (partial x partial y) \``], [`\`  ( partial^2 f) / (partial y partial x) \``, `\`  ( partial^2 f) / (partial x^2) \``]]
  let matTable = arrOfArraysToMat(matrix, matDisplayStyles.determinant)
  cell.appendChild(matTable)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //6
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`  ( partial^2 f) / (partial x^2) = ${results.d2fdx2} \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //7
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`  ( partial^2 f) / (partial y^2) = ${results.d2fdy2}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //8
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` ( partial^2 f) / (partial x partial x) = ${results.d2fdxdy}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)
  //9
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`  ( partial^2 f) / (partial x partial x) = ${results.d2fdxdy}\``)

  console.log('results.hessianMatrix:', results.hessianMatrix)
  matTable = arrOfArraysToMat(results.hessianMatrix, matDisplayStyles.determinant)
  cell.appendChild(matTable)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //10
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`   |H| = ${results.detHessianMatrix}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //11
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode('Get value of h')
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //12
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` g(h) = (x0 +( partial f) / (partial x) * h , y0 + ( partial f) / (partial y) h )\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //13
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` x = ${results.xValueOfGh}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //14
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` y = ${results.yValueOfGh}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //15
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` f(g(h)) = ${results.gh}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  //16
  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`f(g(h)) = ${results.ghAfterSimplify}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` g'(h) = 0 \``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` g'(h) = ${results.dgh}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`  g'(h) = 0 =>  ${results.eq}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\`  g'(h) =  ${results.resultEquation}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` (x,y)\`` + ' coordinates corresponding to this point' + `\`h = ${results.resultEquation}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` x = ${results.newX}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  row = document.createElement('tr')
  cell = document.createElement('td')
  cellText = document.createTextNode(`\` y = ${results.newY}\``)
  cell.appendChild(cellText)
  row.appendChild(cell)
  tableBody.appendChild(row)

  table.appendChild(tableBody)
  table.classList.add('centered')
  resultsDiv.append(table)
  mathJaxRender()
}
